* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.list_box {
  display: flex;
  align-items: center;
  gap: 10px;
}
.list_img {
  width: 50px;
  height: 50px;
  }

  .info_wrapper{
    display: flex;
    align-items: center;
    gap: 15px;
  }